import { createReducer } from './createReducer';
import { combineReducers } from 'redux';
export var reducer = combineReducers({
    todos: createReducer({}, {
        add: function (draft, action) {
            draft[action.id] = { label: action.label, completed: false };
            return draft;
        },
        remove: function (draft, action) {
            delete draft[action.id];
            return draft;
        },
        complete: function (draft, action) {
            draft[action.id].completed = !draft[action.id].completed;
            return draft;
        },
        clear: function (draft) {
            Object.keys(draft).forEach(function (id) {
                if (draft[id].completed) {
                    delete draft[id];
                }
            });
            return draft;
        },
        edit: function (draft, action) {
            draft[action.id].label = action.label;
            return draft;
        }
    }),
    filter: createReducer('all', function (draft, action) {
        return action.filter;
    })
});
